body {
  font-family: '', sans-serif;
  background-color: rosybrown;
}

.search__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-bottom: 30px;
}

.mainTitle {
  display: flex;
  text-transform: uppercase;
  flex-direction: row;
  justify-content: center;
  font-size: 50px;
  color: #644143;
  margin: 80px 0 20px 0;
}

.inputText {
  border-radius: 20px;
  border: none;
  margin: 10px 15px 10px 0px;
  padding: 2px 5px 2px 14px;
  background-color: beige;
  width: 220px;
  height: 30px;
  color: #644143;
}

.search__btn {
  border-radius: 14px;
  border: none;
  margin: 10px 0 10px 0;
  padding: 2px 7px 2px 7px;
  background-color: beige;
  color: #644143;
  &:hover {
    background-color: #644143;
    color: beige;
  }
}

.fav__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: #644143;
  color: beige;
  padding: 15px 15px 15px 15px;
  margin-bottom: 40px;
}

.favParagr {
  padding: 0 0 0 850px;
}

.eachSerie__container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 20px 800px 0 800px;
}

.resultImg {
  display: flex;
  justify-content: space-between;
  width: 150px;
}
